
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100%;
  min-height: 100vh;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  display: flex;
  flex-direction: column;
  color: aliceblue;
}
.App{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.App>h1{
  color: #252525;
  margin: .4em 0;
  font-size: 4rem;
  letter-spacing: 4px;
  text-shadow: 3px 3px 0 #640ec7,
  6px 6px 0 #cadf0e;
}

section{
  width: 500px;
  min-height: 450px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: limegreen;
  border-radius: 25px;
  padding: 2em;
}
button{
  width: 300px;
  height: 50px;
  font-weight: 500;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 22px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

section.quiz{
  justify-content: space-between;
}
section.quiz .options{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section.quiz .options button{
  margin: .3em auto;
  transition: .3s;
}
section.quiz .options button:focus{
  background: #e955e9;
  box-shadow: 0 0 30px rgba(0,0,0,0.4);
  color: aliceblue;
  font-weight: 500;
  letter-spacing: 1px;
  border-radius: 0 25px;
}



section.menu>*{
  margin: .5em;
}
section p{
  padding: .5em;
  font-size: 1.3em;
  color: #252525;
  font-weight: 700;
  text-align: center;
}
@media screen and (max-width:530px) {
  .App{
    padding: 1em;
  }
  section{
    width: 100%;
  }
  section p{
    padding: .1em;
    margin: .5em .1em;
  }
  section.quiz{
    text-align: center;
  }
}
